.wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    margin: 0 auto;
}

.iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: fill;
    outline: none;
    border: none;
    margin: 0 auto;
}

// Responsive
@media (max-width: 319px) {
    
}


@media (min-width: 400px) and (max-width: 739px) {
    .wrapper {
        margin-top: 0px;
    }
}

@media (min-width: 740px) and (max-width: 1023px)  {
}


@media (min-width: 1024px) and (max-width: 1298px) {
    .wrapper {
        margin: 0;
    }
}


