@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;900&display=swap");

html {
    font-size: 62.5%;
}

:root {
    --header-height: 60px;
    --black-color: #100F0F;
    --white-color: #f1f1f1;
    --green-color: #0F3D3E;
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    overflow-y: overlay;
}

// Scrollbar
html *::-webkit-scrollbar {
    border-radius: 0;
    width: 4px;
    height: 6px;
}
html *::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: #0F3D3E;
}

html *::-webkit-scrollbar-track {
    border-radius: 0;
    background-color: rgba(221, 213, 213, 0.05);
}

a {
    text-decoration: none;
    color: unset;
}

// Grid
.grid {
    width: 100%;
    display: block;
    padding: 0;
}

.grid.wide {
    max-width: 1200px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;    /*xung dng khi vt kch thc*/
    margin-left: -4px;
    margin-right: -4px;
}

.row.no-gutters  {
    margin-left: 0;
    margin-right: 0;
}

.col {
    padding-left: 4px;
    padding-right: 4px;
}

.row.no-gutters .col {
    padding-left: 0px;
    padding-right: 0px;
}

